.searchbar{--search-height: 32px;position:relative;margin-right:15px;margin-bottom:10px;background-color:var(--base-color-background);border-radius:15px}.searchbar__container{width:300px;height:var(--search-height);display:flex;justify-content:space-evenly;align-items:center;border:1px solid var(--base-color-grey);box-sizing:border-box;border-radius:15px}.searchbar__input{width:260px;height:20px;border:none;font-size:20px;color:var(--darkGrey);background-color:var(--base-color-background)}.searchbar__input:focus{outline:none}.searchbar__input::-webkit-input-placeholder{color:var(--base-color-grey)}.searchbar__input:-ms-input-placeholder{color:var(--base-color-grey)}.searchbar__input::-ms-input-placeholder{color:var(--base-color-grey)}.searchbar__input::placeholder{color:var(--base-color-grey)}.searchbar__icon{font-size:25px;color:var(--base-color-grey)}.searchbar__items{width:400px;z-index:10;max-height:200px;overflow:scroll;margin:0 0 0 15px;padding:0 5px;background-color:var(--base-color-background);position:absolute;top:var(--search-height);box-shadow:0 4px 6px rgba(0,0,0,0.1)}@media screen and (max-width: 618px){.searchbar__container{width:calc(100vw - 181px)}.searchbar__input{width:calc(100% - 50px)}}

:root{--dark-blue: #22577a;--blue: #0096ca;--lightBlue: #61dafb;--green: #28d7a6;--lightGreen: #e6fff8;--orange: #cf5300;--lightOrange: #fff7f2;--purple: #554b86;--lightPurple: #eeecf9;--red: #df0656;--lightRed: #fff8fa;--black: #222;--darkGrey: #929292;--lightGrey: #cbcbcb;--lightBackground: #f9f9f9;--darkCardsBackground: #273239}html{--main-color-bg: var(--lightBackground);--base-color-background: white;--base-color-link: var(--lightBlue);--base-color: var(--black);--base-color-button: var(--darkGrey);--base-color-grey: var(--lightGrey);--base-color-blue: var(--lightBlue)}html[data-theme='dark']{--main-color-bg: var(--black);--base-color-background: var(--darkCardsBackground);--base-color-link: var(--red);--base-color: var(--lightBackground);--base-color-button: var(--darkCardsBackground);--base-color-grey: var(--darkGrey);--base-color-blue: var(--blue)}.country-item{background-color:white;background-color:var(--base-color-background);border:1px solid white;border:1px solid var(--base-color-background);margin:2px 0px 2px 0px;display:flex;align-items:center;height:60px;box-shadow:0px 7px 14px 3px rgba(153,153,153,0.4)}.country-item:hover{cursor:pointer}.country-item:active{background-color:white;background-color:var(--base-color-background);opacity:0.9;box-shadow:0px 3px 10px 3px rgba(102,102,102,0.4);transform:translateY(2px)}.country-item__name{padding:10px;font-size:20px;color:#222;color:var(--base-color)}.country-item__flag{margin:0 10px 0 10px;width:50px;height:50px}

.OptionButton{display:flex;align-items:center;justify-content:center;width:32px;height:32px;cursor:pointer;font-size:20px;background-color:var(--base-color-button);color:white;border:var(--base-color-button);border-radius:50%;outline:none;box-shadow:0 4px 6px rgba(0,0,0,0.1);margin:2px}.OptionButton:active{background-color:var(--blue);transform:translateY(1px)}

.TopBar{display:flex;justify-content:space-between;align-items:center;width:100%;padding:20px 0 0}.TopBar__options{display:flex;align-items:center}

